import { ADMIN_TOKEN, getLeavesClient, isClientError } from '@mintlify/http-client';
import {
  convertMintDecoratedNavToDocsDecoratedNav,
  DecoratedNavigationConfig,
  upgradeToDocsConfig,
} from '@mintlify/validation';

import { BASE_PATH, INTERNAL_ANALYTICS_WRITE_KEY } from '@/env';
import { PathProps } from '@/pages/_sites/[subdomain]/[[...slug]]';
import { notFound } from '@/types/notFoundType';
import type { PageDataProps, PageProps, PageType } from '@/types/page';
import type { StaticProps } from '@/types/staticProps';
import { getApiReferenceData } from '@/utils/api-reference/getApiReferenceData';
import { applyLegacyThemeConfigs } from '@/utils/applyLegacyThemeConfigs';
import { getDynamicTailwindCss, getTailwindSelectors } from '@/utils/getDynamicTailwindCss';
import { prepareToSerialize } from '@/utils/staticProps/prepareToSerialize';
import { serializeContent } from '@/utils/staticProps/serializeContent';

import { getFavicons } from './getFavicons';
import { getMdx } from './getMdx';
import { getRedirect } from './getRedirect';

async function getStaticProps(
  params: PathProps,
  shouldReturnRawContent: true,
  opts: {
    cacheTag: string;
  }
): Promise<StaticProps<true>>;

async function getStaticProps(
  params: PathProps,
  shouldReturnRawContent?: false,
  opts?: {
    cacheTag: string;
  },
  pageType?: PageType
): Promise<StaticProps<false>>;

async function getStaticProps(
  params: PathProps,
  shouldReturnRawContent?: boolean,
  opts?: {
    cacheTag: string;
  },
  pageType?: PageType
): Promise<StaticProps<typeof shouldReturnRawContent>> {
  // for pages in the `main` project hosted at a custom domain, subdomain is actually the domain
  const { subdomain: domainOrSubdomain, slug } = params;
  const path = slug ? slug.join('/') : 'index';

  const cacheTag = opts?.cacheTag;
  const client = getLeavesClient(
    domainOrSubdomain,
    undefined,
    cacheTag
      ? {
          headers: ADMIN_TOKEN ? { Authorization: `Bearer ${ADMIN_TOKEN}` } : undefined,
          next: {
            tags: [cacheTag],
          },
        }
      : undefined
  );
  let data;
  try {
    data = await client.getStaticProps(path);
  } catch (err) {
    if (isClientError(err) && err.response.status === 404) return notFound;
    throw err;
  }

  const { deployment, navigation, docsDecoratedNav } = data;

  const {
    mintConfig,
    disableAiChat,
    entitlements,
    feedback,
    inkeep,
    subdomain,
    trieve,
    userAuth,
    auth,
    gitSource,
  } = deployment;

  let { docsConfig } = deployment;

  let docsNavWithMetadata: DecoratedNavigationConfig;
  if (mintConfig) {
    docsNavWithMetadata = convertMintDecoratedNavToDocsDecoratedNav({
      type: 'mint',
      mintConfig,
      decoratedNavigation: navigation,
    });
    docsConfig = upgradeToDocsConfig(mintConfig);
  } else if ('docsDecoratedNav' in data) {
    docsNavWithMetadata = docsDecoratedNav as DecoratedNavigationConfig;
  } else {
    docsNavWithMetadata = convertMintDecoratedNavToDocsDecoratedNav({
      type: 'docs',
      docsConfig: docsConfig,
      decoratedNavigation: navigation,
    });
  }

  if (!('page' in data)) {
    const redirect = getRedirect(
      path,
      docsConfig.redirects,
      docsNavWithMetadata,
      docsConfig.errors?.[404]?.redirect === false && path !== 'index'
    );
    if (redirect) return { redirect };
    return notFound;
  }
  // prevent every deployment from being served at every subdirectory
  if (BASE_PATH && deployment.basePath !== BASE_PATH) return notFound;

  const {
    page: { content, metadata },
    snippets,
    shouldIndex,
    cssFiles,
    jsFiles,
    org,
    openApiUrls,
    asyncApiUrls,
    buildId,
  } = data;

  if (docsConfig.redirects) {
    const redirect = getRedirect(path, docsConfig.redirects);
    if (redirect) return { redirect };
  }

  if (metadata.url) {
    return {
      redirect: {
        destination: metadata.url,
        permanent: false,
      },
    };
  }

  // fire off async tasks to be awaited later
  const openApiInfoPromise = Promise.all(
    openApiUrls.map(async ({ url, metadata }) => {
      const res = await fetch(url);
      const document = await res.json();
      return { metadata, document };
    })
  );

  const asyncApiInfoPromise = asyncApiUrls
    ? Promise.all(
        asyncApiUrls.map(async ({ url, metadata }) => {
          const res = await fetch(url);
          const document = await res.json();
          return { metadata, document };
        })
      )
    : Promise.resolve([]);

  const tailwindSelectors = await getTailwindSelectors({ content });

  const mdxPromise = getMdx(
    path,
    content,
    metadata,
    snippets,
    subdomain,
    docsConfig,
    tailwindSelectors,
    pageType ?? 'default'
  );

  const favicons = getFavicons(subdomain);

  const description = await serializeContent(metadata.description);
  const banner = await serializeContent(docsConfig.banner?.content);

  // await promises
  const apiReferenceDataPromise = getApiReferenceData(
    metadata,
    await openApiInfoPromise,
    await asyncApiInfoPromise
  );

  if (shouldReturnRawContent === true) {
    const apiReferenceData = await apiReferenceDataPromise;
    return {
      props: {
        content,
        metadata,
        apiReferenceData,
      },
    };
  }

  const [
    { mdxSource, mdxSourceWithNoJs, mdxExtracts, panelMdxSource, panelMdxSourceWithNoJs },
    apiReferenceData,
  ] = await Promise.all([mdxPromise, apiReferenceDataPromise]);

  const dynamicTailwindCss = await getDynamicTailwindCss({
    content: mdxSource.compiledSource + (panelMdxSource?.compiledSource ?? ''),
  });
  if (dynamicTailwindCss) {
    cssFiles.push({ filePath: 'dynamic-tailwind.css', content: dynamicTailwindCss });
  }

  const pageData: PageDataProps = {
    navWithMetadata: navigation,
    docsNavWithMetadata: docsNavWithMetadata,
    pageMetadata: metadata,
    mintConfig: mintConfig ? applyLegacyThemeConfigs(mintConfig) : undefined,
    docsConfig,
    apiReferenceData,
  };

  const pageProps: PageProps = {
    mdxSource,
    mdxExtracts,
    panelMdxSource,
    panelMdxSourceWithNoJs,
    // This is the description of the page that can contain markdown and is different from the one in `metadata`.
    description,
    pageData,
    feedback,
    favicons,
    subdomain: domainOrSubdomain,
    // TODO: Deprecate this in favor of subdomain
    actualSubdomain: subdomain,
    internalAnalyticsWriteKey: INTERNAL_ANALYTICS_WRITE_KEY,
    inkeep,
    trieve,
    userAuth,
    shouldIndex,
    org,
    cssFiles,
    jsFiles,
    mdxSourceWithNoJs,
    disableAiChat,
    entitlements,
    auth,
    gitSource,
    banner,
    buildId,
  };
  return {
    props: prepareToSerialize(pageProps),
  };
}

export default getStaticProps;
